/*First Section*/
.first-hightlight-wrapper {
    background-image: url("../../commonResources/images/home/ipodPronew-sm.jpg"); 
    height: 580px;
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: initial;
    background-color: #fbfbfd;
    text-align: center;
    padding: 65px 20px;
    color: #fff;
    margin-bottom: 10px;
}
.ipod-caption {
    color: #86868b;
    font-size: 12px;
    line-height: 1.33337;
    font-weight: 400;
    letter-spacing: -0.01em;
    padding-top: 310px;
}

@media (min-width: 768px) {
    /* First section */
    .first-hightlight-wrapper {
        background-image: url("../../commonResources/images/home/ipodPronew.jpg");
        min-height: 692px;
    }
    .ipod-caption {
        padding-top: 415px;
    }
}
